export default {
	data() {
		return {
			currentNumber: 0, // 用来判断active样式类是否s
			curPage: 1,
			status: 0,
			nameList: [],
			id: '',
			acid:'',
			list: [{
					title: "全部"
				},
				{
					title: "充值"
				},
				{
					title: "提现"
				}
			]
		};
	},
	onLoad(options) {
		this.id = options.id
		if(!uni.getStorageSync("token")&&!uni.getStorageSync("SecretKey")){
			this.$base1._isLogin()
		}
		this.getCoreDetail()
	},
	onPullDownRefresh() {
		this.getCoreDetail()
		  setTimeout(function () {
			  uni.stopPullDownRefresh();
		  }, 1000);
	  },
	methods: {
		//充提记录
		getCoreDetail() {
			uni.request({
				url: this.baseUrl + "/recharge-withdraw",
				data:{
					page:1,
					count:100000,
				},
				header: {
					//除注册登录外其他的请求都携带用户token和秘钥
					Authorization: uni.getStorageSync('token')
				},
				success: (res) => {
					console.log(res.data)
					if (this.$base1._indexOf(res.data.status)) {
						this.$base1._isLogin()
					} else  if (res.data.status == 1) {
						this.nameList = res.data.data.list
						
					} else {
						uni.showToast({
							title: res.data.message,
							icon: "none"
						})
					}
				}
			})
		},
		currentInfo(index) {
			this.currentNumber = index;
		     
			if(this.currentNumber==1){
				//充提记录根据返回的Type动态显示 1充值 2提现
				for(var i=0; i<this.nameList.length;i++){
					if(this.nameList[i].Type==2){
					 this.nameList.splice(i--,1)
					}
				}
			} else{
				if(this.currentNumber==2){
					for(var i=0; i < this.nameList.length; i++){
						if(this.nameList[i].Type==1){
						 this.nameList.splice(i--,1)
						}
					}
				}else{
					this.getCoreDetail()
				}
				
			}
			
			
		},
		//处理状态返回的显示值
		showStatus(status){
			if(status){
				if(status=="-1"){
					return "驳回" 
				}else if(status=="0"){
					
					return "待处理"
				}else if(status=="1"){
					
					return "处理中"
				}else if(status=="2"){
					return "已处理"
				}else if(status=="3"){
					
					return "失败"
				}else if(status=="4"){
					
					return "处理成功"
				}
			}
		},
		//状态不同显示不同的颜色
		showColor(status){
			if(status){
				if(status=="-1"){
					return "red" 
				}else if(status=="0"){
					
					return "green"
				}else if(status=="1"){
					
					return "green"
				}else if(status=="2"){
					return "blue"
				}else if(status=="3"){
					
					return "red"
				}else if(status=="4"){
					
					return "blue"
				}
			}
		},
		jumpToManage(index) {
			this.id = this.nameList[index].Id
			this.acid = this.nameList[index].ActivityId
			console.log(this.id)//我的理财id
			console.log(this.acid)//理财活动id
			uni.navigateTo({
				url: "./manage-in?id=" + this.id +"&acid="+this.acid//需要传一个investmentId过去,还需传一个acid过去获取天数
			})
		}
	}
}
